<template>
  <div>
    <h2>Todo list</h2>
	<router-link to="/">Home</router-link>
	<hr>
    <AddTodo 
      @add-todo="addTodo"
    />
	<select v-model="filter">
		<option value="all">All</option>
		<option value="completed">Competed</option>
		<option value="not-completed">Not Completed</option>
	</select>
    <hr>
	<Loader v-if="loading" />
    <TodoList
	  v-else-if="filteredTodos.length"
      v-bind:todos="filteredTodos"
      @remove_todo="removeTodo"
	/>
	<p v-else>No todos!</p>
  </div>
</template>

<script>
import TodoList from '@/components/TodoList';
import AddTodo from '@/components/AddTodo';
import Loader from '@/components/Loader';
export default {
  name: 'App',
  data() {
    return {
	  todos: [
		{id: 1, title: 'Выучить JavaScript', completed: false},
		{id: 2, title: 'Выучить VueJS', completed: false},
		{id: 3, title: 'Сделать что-нибудь крутое', completed: false},
	  ],
	  loading: true,
	  filter: 'all'
    }
  },
  mounted() {
    fetch('https://jsonplaceholder.typicode.com/todos?_limit=0')
  .then(response => response.json())
  .then(json => {
	this.todos = json;
	this.loading = false
  })
  },
//  watch: {
//	  filter(value) {
//
//	  }
//  },
  computed: {
	  filteredTodos() {
		  if (this.filter === 'all') {
			  return this.todos
		  }

		  if (this.filter === 'completed') {
			  return this.todos.filter(t => t.completed)
		  }

		  if (this.filter === 'not-completed') {
			  return this.todos.filter(t => !t.completed)
		  }
	  }
  },
  methods: {
    removeTodo(id) {
      this.todos = this.todos.filter(t => t.id !== id)
    },
    addTodo(todo) {
      this.todos.push(todo);
    }
  },
  components: {
    TodoList, AddTodo, Loader
  }
}
</script>

<style scoped>
select {
	margin-top: 10px;
}
</style>